<!-- src/components/meal-planning/MealPlanDetailModal.vue -->
<template>
  <div class="modal-overlay" @click="$emit('close')">
    <div class="modal-content" @click.stop>
      <div class="modal-header">
        <h2>{{ formatPlanType(mealPlan.plan_type) }} Meal Plan</h2>
        <div class="ai-generation-info">
          <span class="ai-badge">
            <i class="fas fa-brain"></i>
            AI v{{ mealPlan.generation_version || '1.0' }}
          </span>
          <span class="model-badge">{{ mealPlan.ai_model_used || 'AI Model' }}</span>
        </div>
        <button @click="$emit('close')" class="close-btn">
          <i class="fas fa-times"></i>
        </button>
      </div>

      <div class="modal-body">
        <div class="plan-overview">
          <div class="overview-stats">
            <div class="stat-item">
              <span class="stat-label">Date Range:</span>
              <span class="stat-value">{{ formatDateRange(mealPlan.start_date, mealPlan.end_date) }}</span>
            </div>
            <div class="stat-item">
              <span class="stat-label">Total Calories:</span>
              <span class="stat-value">{{ Math.round(mealPlan.total_calories || 0) }}</span>
            </div>
            <div class="stat-item">
              <span class="stat-label">Avg Daily Calories:</span>
              <span class="stat-value">{{ Math.round(mealPlan.avg_daily_calories || 0) }}</span>
            </div>
            <div class="stat-item">
              <span class="stat-label">Total Meals:</span>
              <span class="stat-value">{{ getTotalMeals() }}</span>
            </div>
            <div class="stat-item" v-if="mealPlan.nutritional_balance_score">
              <span class="stat-label">Balance Score:</span>
              <span class="stat-value">{{ mealPlan.nutritional_balance_score.toFixed(1) }}/10</span>
            </div>
            <div class="stat-item" v-if="mealPlan.days_count">
              <span class="stat-label">Days:</span>
              <span class="stat-value">{{ mealPlan.days_count }}</span>
            </div>
          </div>

          <!-- Daily Nutritional Averages -->
          <div class="nutrition-overview" v-if="mealPlan.daily_averages">
            <h4>Daily Nutritional Averages</h4>
            <div class="nutrition-grid">
              <div class="nutrition-item">
                <span class="nutrition-label">Protein:</span>
                <span class="nutrition-value">{{ Math.round(mealPlan.daily_averages.protein || 0) }}g</span>
              </div>
              <div class="nutrition-item">
                <span class="nutrition-label">Carbohydrates:</span>
                <span class="nutrition-value">{{ Math.round(mealPlan.daily_averages.carbs || 0) }}g</span>
              </div>
              <div class="nutrition-item">
                <span class="nutrition-label">Fat:</span>
                <span class="nutrition-value">{{ Math.round(mealPlan.daily_averages.fat || 0) }}g</span>
              </div>
              <div class="nutrition-item">
                <span class="nutrition-label">Calories:</span>
                <span class="nutrition-value">{{ Math.round(mealPlan.daily_averages.calories || 0) }}</span>
              </div>
            </div>
          </div>

          <!-- Quality Scores -->
          <div class="quality-scores" v-if="mealPlan.variety_score || mealPlan.preference_match_score">
            <h4>Plan Quality Metrics</h4>
            <div class="scores-grid">
              <div class="score-item" v-if="mealPlan.nutritional_balance_score">
                <span class="score-label">Nutritional Balance:</span>
                <div class="score-bar">
                  <div class="score-fill" :style="{ width: (mealPlan.nutritional_balance_score * 10) + '%' }"></div>
                </div>
                <span class="score-value">{{ mealPlan.nutritional_balance_score.toFixed(1) }}/10</span>
              </div>
              <div class="score-item" v-if="mealPlan.variety_score">
                <span class="score-label">Variety Score:</span>
                <div class="score-bar">
                  <div class="score-fill" :style="{ width: (mealPlan.variety_score * 10) + '%' }"></div>
                </div>
                <span class="score-value">{{ mealPlan.variety_score.toFixed(1) }}/10</span>
              </div>
              <div class="score-item" v-if="mealPlan.preference_match_score">
                <span class="score-label">Preference Match:</span>
                <div class="score-bar">
                  <div class="score-fill" :style="{ width: (mealPlan.preference_match_score * 10) + '%' }"></div>
                </div>
                <span class="score-value">{{ mealPlan.preference_match_score.toFixed(1) }}/10</span>
              </div>
            </div>
          </div>
        </div>

        <div class="meals-section" v-if="mealPlan.meal_plan_data && mealPlan.meal_plan_data.meals">
          <h3>Detailed Meal Plan</h3>
          <div class="meals-grid">
            <div
              v-for="(dayMeals, date) in mealPlan.meal_plan_data.meals"
              :key="date"
              class="day-meals"
            >
              <h4 class="day-header">{{ formatDate(date) }}</h4>
              <div class="meals-list">
                <div
                  v-for="(meal, index) in dayMeals"
                  :key="index"
                  class="meal-card"
                >
                  <div class="meal-header">
                    <div class="meal-title-section">
                      <h5 class="meal-title">
                        {{ getMealTitle(meal) }}
                      </h5>
                      <span class="meal-type-badge" :class="getMealTypeClass(meal.meal_type || 'meal')">
                        {{ formatMealType(meal.meal_type || 'Meal') }}
                      </span>
                    </div>
                    <div class="meal-meta">
                      <span class="meal-time">
                        <i class="fas fa-clock"></i>
                        {{ getMealTime(meal) }}
                      </span>
                      <span class="meal-cuisine" v-if="meal.recipe?.cuisine || meal.cuisine">
                        <i class="fas fa-globe"></i>
                        {{ meal.recipe?.cuisine || meal.cuisine }}
                      </span>
                    </div>
                  </div>

                  <!-- Recipe Details -->
                  <div class="recipe-details" v-if="meal.recipe || getMealRecipe(meal)">
                    <!-- Cooking Information -->
                    <div class="cooking-info" v-if="getMealRecipe(meal).readyInMinutes || getMealRecipe(meal).prep_time || getMealRecipe(meal).cook_time || getMealRecipe(meal).total_time">
                      <div class="time-info">
                        <span v-if="getMealRecipe(meal).readyInMinutes" class="time-item">
                          <i class="fas fa-hourglass-half"></i>
                          Ready: {{ getMealRecipe(meal).readyInMinutes }}min
                        </span>
                        <span v-if="getMealRecipe(meal).prep_time" class="time-item">
                          <i class="fas fa-cut"></i>
                          Prep: {{ getMealRecipe(meal).prep_time }}min
                        </span>
                        <span v-if="getMealRecipe(meal).cook_time" class="time-item">
                          <i class="fas fa-fire"></i>
                          Cook: {{ getMealRecipe(meal).cook_time }}min
                        </span>
                        <span v-if="getMealRecipe(meal).total_time" class="time-item">
                          <i class="fas fa-hourglass-half"></i>
                          Total: {{ getMealRecipe(meal).total_time }}min
                        </span>
                        <span v-if="getMealRecipe(meal).servings" class="time-item">
                          <i class="fas fa-users"></i>
                          Serves: {{ getMealRecipe(meal).servings }}
                        </span>
                      </div>
                    </div>

                    <!-- Nutritional Information -->
                    <div class="meal-nutrition">
                      <h6>Nutritional Information</h6>
                      <div class="nutrition-details">
                        <div class="nutrition-item">
                          <span class="nutrition-label">Calories:</span>
                          <span class="nutrition-value">{{ getNutritionValue(getMealRecipe(meal), 'calories') }}</span>
                        </div>
                        <div class="nutrition-item">
                          <span class="nutrition-label">Protein:</span>
                          <span class="nutrition-value">{{ getNutritionValue(getMealRecipe(meal), 'protein') }}g</span>
                        </div>
                        <div class="nutrition-item">
                          <span class="nutrition-label">Carbs:</span>
                          <span class="nutrition-value">{{ getNutritionValue(getMealRecipe(meal), 'carbs') }}g</span>
                        </div>
                        <div class="nutrition-item">
                          <span class="nutrition-label">Fat:</span>
                          <span class="nutrition-value">{{ getNutritionValue(getMealRecipe(meal), 'fat') }}g</span>
                        </div>
                      </div>
                    </div>

                    <!-- Ingredients -->
                    <div class="ingredients-section" v-if="getMealIngredients(meal).length">
                      <h6>Ingredients</h6>
                      <div class="ingredients-list">
                        <div 
                          v-for="(ingredient, idx) in getMealIngredients(meal)" 
                          :key="idx"
                          class="ingredient-item"
                        >
                          <span class="ingredient-quantity">{{ ingredient.quantity || ingredient.amount || '' }}</span>
                          <span class="ingredient-unit">{{ ingredient.unit || '' }}</span>
                          <span class="ingredient-name">{{ ingredient.name || ingredient.original || ingredient.text }}</span>
                        </div>
                      </div>
                    </div>

                    <!-- Instructions -->
                    <div class="instructions-section" v-if="normalizedInstructions(meal).length">
                      <h6>Instructions</h6>
                      <div class="instructions-list">
                        <div 
                          v-for="(instruction, idx) in normalizedInstructions(meal)" 
                          :key="idx"
                          class="instruction-item"
                        >
                          <span class="instruction-number">{{ instruction.number }}</span>
                          <span class="instruction-text">{{ instruction.step }}</span>
                        </div>
                      </div>
                    </div>

                    <!-- Target Information -->
                    <div class="target-info" v-if="meal.target_calories || meal.target_protein || meal.target_carbs || meal.target_fat">
                      <h6>Meal Targets</h6>
                      <div class="targets-grid">
                        <div class="target-item" v-if="meal.target_calories">
                          <span class="target-label">Target Calories:</span>
                          <span class="target-value">{{ Math.round(meal.target_calories) }}</span>
                        </div>
                        <div class="target-item" v-if="meal.target_protein">
                          <span class="target-label">Target Protein:</span>
                          <span class="target-value">{{ Math.round(meal.target_protein) }}g</span>
                        </div>
                        <div class="target-item" v-if="meal.target_carbs">
                          <span class="target-label">Target Carbs:</span>
                          <span class="target-value">{{ Math.round(meal.target_carbs) }}g</span>
                        </div>
                        <div class="target-item" v-if="meal.target_fat">
                          <span class="target-label">Target Fat:</span>
                          <span class="target-value">{{ Math.round(meal.target_fat) }}g</span>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div class="meal-actions">
                    <button
                                                @click="toggleRecipeSave(meal.recipe || meal)"
                      class="btn btn-sm"
                      :class="(meal.recipe || meal)?.is_saved_by_user ? 'btn-outline-danger' : 'btn-primary'"
                      :disabled="savingRecipe === (meal.recipe || meal)?.id"
                    >
                      <i v-if="savingRecipe === (meal.recipe || meal)?.id" class="fas fa-spinner fa-spin"></i>
                      <i v-else :class="(meal.recipe || meal)?.is_saved_by_user ? 'fas fa-heart' : 'far fa-heart'"></i>
                      {{ savingRecipe === (meal.recipe || meal)?.id ? ((meal.recipe || meal)?.is_saved_by_user ? 'Removing...' : 'Saving...') : ((meal.recipe || meal)?.is_saved_by_user ? 'Remove' : 'Save') }}
                    </button>
                    <button
                      @click="regenerateMeal(date, meal.meal_type || inferMealTypeFromTime(new Date()))"
                      class="btn btn-sm btn-outline"
                    >
                      <i class="fas fa-redo"></i>
                      Regenerate
                    </button>
                    <button
                      @click="getAlternatives(date, meal.meal_type || inferMealTypeFromTime(new Date()))"
                      class="btn btn-sm btn-secondary"
                    >
                      <i class="fas fa-exchange-alt"></i>
                      Alternatives
                    </button>
                    <button
                      @click="viewRecipeDetails(meal)"
                      class="btn btn-sm btn-info"
                    >
                      <i class="fas fa-info-circle"></i>
                      Recipe Details
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div v-else class="no-meals">
          <i class="fas fa-utensils"></i>
          <p>No meal details available</p>
        </div>
      </div>

      <div class="modal-footer">
        <button @click="$emit('close')" class="btn btn-secondary">Close</button>
        <button @click="generateShoppingList" class="btn btn-success">
          <i class="fas fa-shopping-cart"></i>
          Generate Shopping List
        </button>
        <button @click="analyzePlan" class="btn btn-info">
          <i class="fas fa-chart-line"></i>
          AI Analysis
        </button>
        <button @click="deletePlan" class="btn btn-danger">
          <i class="fas fa-trash"></i>
          Delete Plan
        </button>
      </div>
    </div>

    <!-- Recipe Detail Modal -->
    <recipe-detail-modal
      v-if="showRecipeModal && selectedRecipe"
      :recipe="selectedRecipe"
      @close="closeRecipeModal"
      @recipe-saved="onRecipeSaved"
    />

    <!-- Shopping List Modal -->
    <shopping-list-modal
      v-if="showShoppingListModal"
      :shopping-list="shoppingListData"
      :loading="shoppingListLoading"
      :error="shoppingListError"
      @close="closeShoppingListModal"
      @retry="generateShoppingList"
    />
  </div>
</template>

<script>
import RecipeDetailModal from './RecipeDetailModal.vue'
import ShoppingListModal from './ShoppingListModal.vue'
import { makeShoppingListRequest } from '@/utils/fetchUtils'

export default {
  name: 'MealPlanDetailModal',
  components: {
    RecipeDetailModal,
    ShoppingListModal
  },
  props: {
    mealPlan: {
      type: Object,
      required: true
    }
  },
  emits: ['close', 'regenerate-meal', 'get-alternatives', 'analyze-plan', 'delete-plan'],
  data() {
    return {
      savingRecipe: null, // Track which recipe is being saved
      showRecipeModal: false,
      selectedRecipe: null,
      showShoppingListModal: false,
      shoppingListData: null,
      shoppingListLoading: false,
      shoppingListError: null
    }
  },
  computed: {
    // Add computed property to normalize instructions
    normalizedInstructions() {
      return (meal) => {
        const instr = meal?.recipe?.instructions || meal?.instructions || []
        
        // Handle empty instructions
        if (!instr || instr.length === 0) {
          return []
        }
        
        // If it's already an array of objects with step property, extract the step text
        if (instr.length && typeof instr[0] === 'object' && instr[0].step) {
          return instr.map((stepObj, i) => ({
            number: stepObj.number || i + 1,
            step: stepObj.step,
            description: stepObj.step
          }))
        }
        
        // If it's an array of strings, convert to objects
        if (instr.length && typeof instr[0] === 'string') {
          return instr.map((stepText, i) => ({
            number: i + 1,
            step: stepText,
            description: stepText
          }))
        }
        
        // If it's an array of objects without step property, try to extract the step text
        if (instr.length && typeof instr[0] === 'object') {
          return instr.map((stepObj, i) => ({
            number: stepObj.number || i + 1,
            step: stepObj.step || stepObj.description || stepObj.instruction || `Step ${i + 1}`,
            description: stepObj.step || stepObj.description || stepObj.instruction || `Step ${i + 1}`
          }))
        }
        
        return []
      }
    }
  },
  mounted() {
    console.log('=== MealPlanDetailModal Mounted ===')
    console.log('Meal Plan ID:', this.mealPlan?.id)
    console.log('Plan Type:', this.mealPlan?.plan_type)
    console.log('Has meal_plan_data:', !!this.mealPlan?.meal_plan_data)
    console.log('Has meals:', !!this.mealPlan?.meal_plan_data?.meals)
    
    if (this.mealPlan?.meal_plan_data?.meals) {
      const mealDates = Object.keys(this.mealPlan.meal_plan_data.meals)
      console.log('Available meal dates:', mealDates)
      console.log('Total meal days:', mealDates.length)
      
      // Log first day's meals as sample
      if (mealDates.length > 0) {
        const firstDate = mealDates[0]
        const firstDayMeals = this.mealPlan.meal_plan_data.meals[firstDate]
        console.log(`Sample meals for ${firstDate}:`, firstDayMeals)
      }
    } else {
      console.log('No meal data available - checking structure:')
      console.log('mealPlan keys:', Object.keys(this.mealPlan || {}))
      if (this.mealPlan?.meal_plan_data) {
        console.log('meal_plan_data keys:', Object.keys(this.mealPlan.meal_plan_data))
      }
    }
    console.log('=====================================')
    
    // Add escape key listener
    document.addEventListener('keydown', this.handleEscapeKey)
  },

  beforeUnmount() {
    // Remove escape key listener
    document.removeEventListener('keydown', this.handleEscapeKey)
  },
  methods: {
    formatPlanType(type) {
      if (!type) return 'Unknown'
      return type.charAt(0).toUpperCase() + type.slice(1)
    },

    formatDateRange(startDate, endDate) {
      if (!startDate || !endDate) {
        console.log('formatDateRange: Missing dates - start:', startDate, 'end:', endDate)
        return 'Date unknown'
      }
      
      try {
        const startDateObj = new Date(startDate)
        const endDateObj = new Date(endDate)
        
        if (isNaN(startDateObj.getTime()) || isNaN(endDateObj.getTime())) {
          console.log('formatDateRange: Invalid dates - start:', startDate, 'end:', endDate)
          return 'Invalid date range'
        }
        
        const start = startDateObj.toLocaleDateString()
        const end = endDateObj.toLocaleDateString()

        if (startDate === endDate) {
          return start
        }
        return `${start} - ${end}`
      } catch (error) {
        console.error('formatDateRange error:', error, 'for dates:', startDate, endDate)
        return 'Date error'
      }
    },

    formatDate(dateStr) {
      try {
        if (!dateStr) {
          console.log('formatDate: dateStr is null/undefined')
          return 'Unknown Date'
        }
        
        // Handle meal type strings for legacy daily plans (backwards compatibility)
        const mealTypeLabels = {
          'breakfast': 'Breakfast Meals',
          'lunch': 'Lunch Meals', 
          'dinner': 'Dinner Meals',
          'snack': 'Snack Meals'
        }
        
        if (mealTypeLabels[dateStr.toLowerCase()]) {
          console.log('formatDate: Detected legacy meal type structure:', dateStr)
          return mealTypeLabels[dateStr.toLowerCase()]
        }
        
        // Try to parse as date for date-based plans
        const date = new Date(dateStr)
        if (isNaN(date.getTime())) {
          console.log('formatDate: Invalid date for input:', dateStr)
          return dateStr // Return original string if can't parse
        }
        
        return date.toLocaleDateString('en-US', {
          weekday: 'long',
          month: 'short',
          day: 'numeric'
        })
      } catch (error) {
        console.error('formatDate error:', error, 'for input:', dateStr)
        return dateStr || 'Unknown Date'
      }
    },

    formatMealType(type) {
      if (!type) return 'Meal'
      // Handle different possible meal type formats
      const cleanType = String(type).toLowerCase().trim()
      const typeMap = {
        'breakfast': 'Breakfast',
        'lunch': 'Lunch', 
        'dinner': 'Dinner',
        'snack': 'Snack',
        'dessert': 'Dessert'
      }
      return typeMap[cleanType] || type.charAt(0).toUpperCase() + type.slice(1)
    },

    getMealTitle(meal) {
      console.log('getMealTitle called for meal:', meal)
      
      // Handle direct recipe objects (after regeneration)
      if (meal?.title) {
        return meal.title
      }
      
      if (meal?.name) {
        return meal.name
      }
      
      // If we have a recipe with a title, use that
      if (meal?.recipe?.title) {
        return meal.recipe.title
      }
      
      // If we have a recipe with a name, use that
      if (meal?.recipe?.name) {
        return meal.recipe.name
      }
      
      // Fallback to meal type
      const mealType = this.formatMealType(meal?.meal_type)
      console.log('getMealTitle fallback to meal type:', mealType)
      return mealType || 'Meal'
    },

    inferMealTypeFromTime(date) {
      const hour = date.getHours()
      if (hour >= 5 && hour < 11) return 'breakfast'
      if (hour >= 11 && hour < 16) return 'lunch'
      if (hour >= 16 && hour < 22) return 'dinner'
      return 'snack'
    },

    getMealTime(meal) {
      console.log('getMealTime called for meal:', meal)
      
      // Check various possible time fields
      if (meal?.time) {
        return meal.time
      }
      if (meal?.scheduled_time) {
        return meal.scheduled_time
      }
      
      // Handle nested recipe time fields (for backwards compatibility)
      if (meal?.recipe?.time) {
        return meal.recipe.time
      }
      if (meal?.recipe?.scheduled_time) {
        return meal.recipe.scheduled_time
      }
      
      // Default times based on meal type
      const mealType = meal?.meal_type?.toLowerCase()
      const defaultTimes = {
        'breakfast': '08:00',
        'lunch': '12:30',
        'dinner': '19:00',
        'snack': '15:00'
      }
      
      const defaultTime = defaultTimes[mealType] || '12:00'
      console.log('getMealTime using default time:', defaultTime, 'for meal type:', mealType)
      return defaultTime
    },

    getMealRecipe(meal) {
      // Return the recipe object, handling both wrapped and direct structures
      return meal?.recipe || meal || {}
    },

    getMealIngredients(meal) {
      const recipe = this.getMealRecipe(meal)
      
      // Handle different ingredient structures
      if (recipe.ingredients && Array.isArray(recipe.ingredients)) {
        return recipe.ingredients
      }
      
      if (recipe.extendedIngredients && Array.isArray(recipe.extendedIngredients)) {
        return recipe.extendedIngredients
      }
      
      return []
    },

    getMealTypeClass(type) {
      if (!type) return 'meal'
      return String(type).toLowerCase()
    },

    getTotalMeals() {
      if (!this.mealPlan.meal_plan_data?.meals) return 0

      let total = 0
      Object.values(this.mealPlan.meal_plan_data.meals).forEach(dayMeals => {
        if (Array.isArray(dayMeals)) {
          total += dayMeals.length
        }
      })
      return total
    },

    // Helper method to get nutrition values from different possible structures
    getNutritionValue(recipe, nutrient) {
      if (!recipe) return 0
      
      // Try estimated_nutrition first (from your API response)
      if (recipe.estimated_nutrition && recipe.estimated_nutrition[nutrient] !== undefined) {
        return Math.round(recipe.estimated_nutrition[nutrient])
      }
      
      // Fallback to nutrition object
      if (recipe.nutrition && recipe.nutrition[nutrient] !== undefined) {
        return Math.round(recipe.nutrition[nutrient])
      }

      // Try other common naming patterns
      const nutrientMappings = {
        'calories': ['calories_per_serving', 'kcal'],
        'protein': ['protein_per_serving'],
        'carbs': ['carbs_per_serving', 'carbohydrates'],
        'fat': ['fat_per_serving', 'fats']
      }

      if (nutrientMappings[nutrient]) {
        for (const mapping of nutrientMappings[nutrient]) {
          if (recipe[mapping] !== undefined) {
            return Math.round(recipe[mapping])
          }
        }
      }
      
      return 0
    },

    regenerateMeal(date, mealType) {
      console.log('=== regenerateMeal called ===')
      console.log('Date:', date)
      console.log('MealType (raw):', mealType)
      console.log('MealType type:', typeof mealType)
      
      // Ensure mealType is properly defined
      if (!mealType || mealType === 'undefined' || mealType === 'null') {
        console.error('Invalid mealType provided:', mealType)
        // Try to infer from time or provide a fallback
        const inferredMealType = this.inferMealTypeFromTime(new Date())
        console.log('Using inferred meal type:', inferredMealType)
        mealType = inferredMealType
      }
      
      // Clean up the mealType string
      const cleanMealType = String(mealType).toLowerCase().trim()
      
      console.log('Final cleaned mealType:', cleanMealType)
      console.log('=============================')
      
      this.$emit('regenerate-meal', {
        day: date,
        mealType: cleanMealType
      })
    },

    getAlternatives(date, mealType) {
      console.log('=== getAlternatives called ===')
      console.log('Date:', date)
      console.log('MealType (raw):', mealType)
      
      // Ensure mealType is properly defined
      if (!mealType || mealType === 'undefined' || mealType === 'null') {
        console.error('Invalid mealType provided for alternatives:', mealType)
        const inferredMealType = this.inferMealTypeFromTime(new Date())
        console.log('Using inferred meal type for alternatives:', inferredMealType)
        mealType = inferredMealType
      }
      
      // Clean up the mealType string
      const cleanMealType = String(mealType).toLowerCase().trim()
      
      console.log('Final cleaned mealType for alternatives:', cleanMealType)
      console.log('==================================')
      
      this.$emit('get-alternatives', {
        day: date,
        mealType: cleanMealType
      })
    },

    async toggleRecipeSave(recipe) {
      // Check if we're already processing this recipe
      if (this.savingRecipe === (recipe.id || recipe.spoonacular_id)) {
        return
      }

      this.savingRecipe = recipe.id || recipe.spoonacular_id

      try {
        console.log('Toggling save status for recipe:', recipe)
        
        // Check if this is a fallback recipe that cannot be saved
        const recipeId = recipe.spoonacular_id || recipe.id
        if (recipeId && String(recipeId).startsWith('fallback_')) {
          this.$toast?.error?.('This is a temporary recipe that cannot be saved to your collection.') ||
          alert('This is a temporary recipe that cannot be saved to your collection.')
          return
        }
        
        // Import the API service
        const { mealPlanningApi } = await import('@/services/mealPlanningApi')
        
        let response
        
        if (recipe.is_saved_by_user) {
          // Remove from saved recipes
          if (!recipe.id) {
            throw new Error('Cannot remove recipe: No recipe ID found')
          }
          
          // Check if this is a fallback recipe ID
          const isFallbackRecipe = recipe.id.toString().startsWith('fallback_')
          if (isFallbackRecipe) {
            throw new Error('Cannot remove fallback recipe: Not saved in database')
          }
          
          response = await mealPlanningApi.removeRecipeFromMyCollection(recipe.id)
          
          // Update local state
          recipe.is_saved_by_user = false
          
          const message = response.data?.message || 'Recipe removed from your collection!'
          this.$toast?.success?.(message) || alert(message)
          
        } else {
          // Save recipe - validate we have essential data
          if (!recipe.title && !recipe.name) {
            throw new Error('Recipe must have a title to be saved')
          }
          
          // Prepare recipe data for saving with better validation
          const recipeData = {
            title: recipe.title || recipe.name || 'Untitled Recipe',
            summary: recipe.summary || '',
            cuisine: recipe.cuisine || '',
            meal_type: recipe.meal_type || 'dinner',
            servings: recipe.servings || 4,
            prep_time_minutes: recipe.prep_time || recipe.prep_time_minutes || 30,
            cook_time_minutes: recipe.cook_time || recipe.cook_time_minutes || 0,
            total_time_minutes: recipe.total_time || recipe.total_time_minutes || 30,
            difficulty_level: recipe.difficulty_level || 'medium',
            spoonacular_id: recipe.spoonacular_id || recipe.id,
            ingredients_data: recipe.ingredients || recipe.ingredients_data || [],
            instructions: recipe.instructions || [],
            calories_per_serving: this.getNutritionValue(recipe, 'calories') || 300,
            protein_per_serving: this.getNutritionValue(recipe, 'protein') || 15,
            carbs_per_serving: this.getNutritionValue(recipe, 'carbs') || 30,
            fat_per_serving: this.getNutritionValue(recipe, 'fat') || 10,
            fiber_per_serving: this.getNutritionValue(recipe, 'fiber') || 0,
            dietary_tags: recipe.dietary_tags || [],
            allergens: recipe.allergens || [],
            image_url: recipe.image_url || recipe.image || '',
            source_url: recipe.source_url || '',
            source_type: 'spoonacular'
          }
          
          response = await mealPlanningApi.saveRecipeFromMealPlan(recipeData)
          
          // Update local state
          recipe.is_saved_by_user = true
          
          // Show success message
          const message = response.data?.message || 'Recipe saved to your collection!'
          this.$toast?.success?.(message) || alert(message)
        }
        
      } catch (error) {
        console.error('Error toggling recipe save status:', error)
        
        let errorMessage = 'An error occurred while saving the recipe'
        
        if (!navigator.onLine) {
          errorMessage = 'No internet connection. Please check your network and try again.'
        } else if (error.response?.status === 400) {
          // Handle specific backend errors
          const backendError = error.response.data
          if (backendError.error === 'Cannot save fallback recipe') {
            errorMessage = backendError.message || 'This is a temporary recipe that cannot be saved.'
          } else {
            errorMessage = backendError.message || 'Invalid recipe data. Please try again.'
          }
        } else if (error.response?.status === 404) {
          errorMessage = 'Recipe not found. This may be a temporary recipe that cannot be saved.'
        } else if (error.response?.status === 403) {
          errorMessage = 'You don\'t have permission to perform this action. Please log in again.'
        } else if (error.response?.status === 500) {
          const backendError = error.response.data
          errorMessage = backendError.details ? 
            `Server error: ${backendError.details}` : 
            'A server error occurred. Please try again later.'
        } else if (error.message) {
          errorMessage = error.message
        }
        
        this.$toast?.error?.(errorMessage) || alert(errorMessage)
        
      } finally {
        this.savingRecipe = null
      }
    },

    viewRecipeDetails(meal) {
      if (meal) {
        // If meal has a recipe property, use that; otherwise use the meal itself as the recipe
        const recipe = meal.recipe || meal
        
        // Ensure the recipe has the required fields for the modal
        if (!recipe.is_saved_by_user) {
          recipe.is_saved_by_user = false
        }
        
        this.selectedRecipe = recipe
        this.showRecipeModal = true
      }
    },

    async generateShoppingList() {
      try {
        this.shoppingListLoading = true
        this.shoppingListError = null
        this.showShoppingListModal = true

        const data = await makeShoppingListRequest(
          `/api/meal-planning/meal-plans/${this.mealPlan.id}/generate_shopping_list/`,
          {
            exclude_items: [],
            group_by_category: true
          },
          this.$store.state.auth.token
        )
        this.shoppingListData = data.shopping_list
        this.shoppingListLoading = false

        // Show success message
        this.$toast?.success?.('Shopping list generated successfully!') ||
        console.log('Shopping list generated successfully!')

      } catch (error) {
        console.error('Error generating shopping list:', error)
        
        // Try to provide a fallback shopping list if the API fails
        if (error.message.includes('JSON') || error.message.includes('Empty response')) {
          console.log('Attempting fallback shopping list generation...')
          try {
            // Generate basic shopping list from meal plan recipes
            const ingredients = []
            const recipeNames = []
            
            console.log('MealPlan structure:', this.mealPlan)
            console.log('MealPlan meal_plan_data:', this.mealPlan?.meal_plan_data)
            
            if (this.mealPlan?.meal_plan_data?.meals) {
              // Handle the new meal plan structure
              Object.entries(this.mealPlan.meal_plan_data.meals).forEach(([date, meals]) => {
                console.log(`Processing meals for ${date}:`, meals)
                meals.forEach(meal => {
                  if (meal.title) {
                    recipeNames.push(meal.title)
                  }
                  
                  // Check different possible ingredient structures
                  let mealIngredients = []
                  
                  if (meal.extendedIngredients && Array.isArray(meal.extendedIngredients)) {
                    // Spoonacular format
                    mealIngredients = meal.extendedIngredients
                  } else if (meal.ingredients && Array.isArray(meal.ingredients)) {
                    // Fallback format - simple string array
                    mealIngredients = meal.ingredients.map(ing => ({
                      name: ing,
                      amount: 1,
                      unit: ''
                    }))
                  } else if (meal.recipe?.extendedIngredients) {
                    // Recipe nested format
                    mealIngredients = meal.recipe.extendedIngredients
                  } else if (meal.recipe?.ingredients) {
                    // Recipe nested format with simple ingredients
                    mealIngredients = meal.recipe.ingredients.map(ing => ({
                      name: typeof ing === 'string' ? ing : (ing.name || ing.original || 'Unknown'),
                      amount: ing.amount || 1,
                      unit: ing.unit || ''
                    }))
                  }
                  
                  console.log(`Found ${mealIngredients.length} ingredients for meal:`, meal.title)
                  
                  mealIngredients.forEach(ingredient => {
                    ingredients.push({
                      name: ingredient.name || ingredient.original || 'Unknown ingredient',
                      quantity: `${ingredient.amount || 1} ${ingredient.unit || ''}`.trim(),
                      checked: false,
                      notes: []
                    })
                  })
                })
              })
            }
            
            console.log(`Total ingredients found: ${ingredients.length}`)
            console.log('Recipe names:', recipeNames)
            
            const startDate = this.mealPlan?.meal_plan_data?.start_date || new Date().toISOString()
            const endDate = this.mealPlan?.meal_plan_data?.end_date || new Date().toISOString()
            
            this.shoppingListData = {
              metadata: {
                total_items: ingredients.length,
                generated_from: 'meal_plan',
                start_date: startDate,
                end_date: endDate,
                recipe_names: recipeNames
              },
              categories: {
                'other': {
                  name: 'Other Items',
                  icon: '🛒',
                  order: 1,
                  item_count: ingredients.length,
                  items: ingredients
                }
              }
            }
            this.shoppingListLoading = false
            this.$toast?.warning?.('Shopping list generated with basic ingredients (API unavailable)') ||
            console.log('Fallback shopping list generated')
            return
          } catch (fallbackError) {
            console.error('Fallback generation also failed:', fallbackError)
          }
        }
        
        this.shoppingListError = error.message || 'Failed to generate shopping list'
        this.shoppingListLoading = false
        
        // Show error message
        this.$toast?.error?.(`Error: ${this.shoppingListError}`) ||
        alert(`Error generating shopping list: ${this.shoppingListError}`)
      }
    },

    analyzePlan() {
      this.$emit('analyze-plan', this.mealPlan)
    },

    deletePlan() {
      this.$emit('delete-plan', this.mealPlan)
    },

    closeRecipeModal() {
      this.showRecipeModal = false
      this.selectedRecipe = null
    },

    closeShoppingListModal() {
      this.showShoppingListModal = false
      this.shoppingListData = null
      this.shoppingListError = null
      this.shoppingListLoading = false  // Reset loading state
    },

    async onRecipeSaved(savedRecipe) {
      console.log('Recipe saved from meal plan detail:', savedRecipe)
      // Show success message
      this.$toast?.success?.('Recipe saved to your collection!') ||
      alert('Recipe saved to your collection!')
    },

    handleEscapeKey(event) {
      if (event.key === 'Escape') {
        this.$emit('close')
      }
    }
  }
}
</script>

