  animation: loading 1.5s infinite;
  border-radius: 8px;
  height: 20px;
  
  &.loading-title {
    height: 24px;
    width: 60%;
    margin-bottom: 12px;
  }
  
  &.loading-text {
    height: 16px;
    width: 80%;
  }
}

@keyframes loading {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}
